import request from '@/utils/request'

// 
export function signIn(data) {
  return request({
    url: '/syslogin/do_login',
    method: 'post',
    data: data
  })
}

//  publicKey
export function queryPublicKey(data) {
  return request({
    url: '/encryption/getPublicKey',
    method: 'post',
    data: data
  })
}
