<template>
  <div class="role-manage wrapper-content">
    <div class="role-manage-query comSty">
      <el-form :inline="true">
        <el-form-item label="">
          <el-input placeholder="" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search" @click="refresh(true)" title=""> </el-button>
          <el-button icon="el-icon-plus" @click="doAdd" type="primary" title=""> </el-button>
          <el-button @click="doEdit(rowData)" icon="el-icon-edit"> </el-button>
          <el-button @click="doDelete(rowData)" icon="el-icon-delete"> </el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-page">
      <el-scrollbar style="height: 100%">
        <div class="role-manage-tab comSty">
          <el-table :data="dataList" ref="roleTable" class="comTable" @row-click="rowClick" highlight-current-row
                    stripe>
            <el-table-column prop="id" label="" width="50">
              <template slot-scope="scope"><span>{{ ( pageNum - 1 ) * pageSize + scope.$index + 1 }} </span></template>
            </el-table-column>
            <el-table-column prop="login_name" label=""></el-table-column>
            <el-table-column prop="user_name" label=""></el-table-column>
            <el-table-column prop="login_password" label="" show-overflow-tooltip></el-table-column>
            <el-table-column prop="create_time_format" label=""></el-table-column>
            <el-table-column label="" width="200">
              <template slot-scope="props">
                <el-button type="text" @click="resetPassWord(props.row)" icon="el-icon-refresh"></el-button>
                <el-button type="text" @click="editPassWord(props.row)" icon="el-icon-edit"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="role-manage-page pageStyle" v-if="total > 0">
          <el-pagination layout="total, sizes, prev, pager, next" :current-page="pageNum" :total="total"
                         @current-change="(curr) => {this.pageNum = curr ; this.refresh()}"
                         :page-sizes="[10, 20, 30, 50, 100]"
                         @size-change="(s) => {this.pageSize = s ; this.refresh()}"
                         :page-size="pageSize">
          </el-pagination>
        </div>
      </el-scrollbar>
    </div>
    <role-dialog ref="dialog" :refresh="refresh"></role-dialog>
    <edit-dialog ref="editDialog" :refresh="refresh"></edit-dialog>
    <edit-pass-word-dialog ref="editPassWordDialog" :refresh="refresh"></edit-pass-word-dialog>
  </div>
</template>
<script>
    import roleDialog from './components/role-dialog'
    import editDialog from './components/edit-dialog'
    import editPassWordDialog from './components/edit-password-dialog'
    import {deleteAdmin, queryAdminDataList, resetPassword} from '@/api/sys/role.js'
    import {testingObject} from '@/api/sys/station.js'

    export default {
        components: {roleDialog, editDialog, editPassWordDialog},
        data: function () {
            return {
                rowData: {},
                total: 1, // 
                pageNum: 1, // 
                pageSize: 10, // 
                dataList: [], // 
                form: {
                    id: null, // 
                    name: '', // 
                    remark: null // 
                },
                loading: false // 
            }
        },
        created: function () {
            this.refresh()
        },
        methods: {
            // 
            refresh() {
                let a = new FormData()
                a.append("login_name", this.form.name)
                a.append("pageNum", this.pageNum)
                a.append("pageSize", this.pageSize)
                queryAdminDataList(a).then(res => {
                    this.dataList = res.data.list
                    this.total = res.data.total
                })
            },
            //   row 
            rowClick(row, column, event) {
                if (this.rowData.id === undefined) {
                    this.rowData = row
                    this.$refs.roleTable.setCurrentRow(row)
                } else if (this.rowData.id === row.id) {
                    this.$refs.roleTable.setCurrentRow()
                    this.rowData = {}
                } else {
                    this.rowData = row
                    this.$refs.roleTable.setCurrentRow(row)
                }
            },
            // 
            resetPassWord(row) {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let a = new FormData()
                    a.append("id", row.id)
                    resetPassword(a).then(res => {
                        // 
                        this.$alert('' + res.data.login_password, '', {
                            confirmButtonText: '',
                        });
                    })
                })
            },
            // 
            editPassWord(row) {
                this.$refs['editPassWordDialog'].editPassDialog(row)
            },
            // 
            doAdd() {
                this.$refs['dialog'].addDialog()
            },
            // 
            doEdit(row) {
                if (testingObject(row)) {
                    this.$message.warning('')
                } else {
                    this.$refs['editDialog'].editDialog(row)
                }
            },
            // 
            doDelete(row) {
                if (testingObject(row)) {
                    this.$message.warning('')
                } else {
                    this.$confirm('?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        let a = new FormData()
                        a.append("id", row.id)
                        deleteAdmin(a).then(res => {
                            if (res.code === 1000) {
                                this.$message.success('')
                                this.refresh()
                            } else {
                                this.$message.success('')
                            }
                        })
                    })
                }
            }
        }
    }
</script>
<style lang="less" scoped>
  .role-manage {
    display: flex;
    flex-direction: column;
    .role-manage-query {
      display: flex;
      /*justify-content: center;*/
      align-items: center;
      border-bottom: 1px solid #ccc;
    }
    .table-page {
      flex-grow: 1;
      overflow-y: auto;
    }
  }
</style>
