import request from '@/utils/request'

// 
export function queryAdminDataList (requestData) {
  return request({
    url: '/cms/user/queryPageList',
    method: 'post',
    data: requestData,
    // headers:
  })
}

// 
export function addAdmin (data) {
  return request({
    url: '/cms/user/insert',
    method: 'post',
    data: data
  })
}

// 
export function editAdmin (data) {
  return request({
    url: '/cms/user/update',
    method: 'post',
    data: data
  })
}

// 
export function deleteAdmin(data)  {
  return request({
    url: '/cms/user/delete',
    method: 'post',
    data: data
  })
}

//  /cms/user/ resetPassword
export function resetPassword (data) {
  return request({
    url: '/cms/user/resetPassword',
    method: 'post',
    data: data
  })
}

//  /cms/user/updatePassword
export function editPassword (data) {
  return request({
    url: '/cms/user/updatePassword',
    method: 'post',
    data: data
  })
}
